.score-card {
  display: flex;
  flex-direction: row;
}

.player-dot {
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  margin-right: 5rem;
  transform: translateY(-25%);
  display: inline-block;
  margin: auto 0;
  box-sizing: border-box;
}

.player-label {
  margin-left: 0.5rem;
}

.player-dot::after {
  content: "\00a0";
}

.player-dot.player-1 {
  background-color: var(--player-1);
  border: 4px solid var(--player-1);
}

.player-dot.player-2 {
  background-color: var(--player-2);
  border: 4px solid var(--player-2);
}

.player-dot.isturn {
  background-color: #afa;
}
